#package-header {
    overflow: auto;
    background-color: rgba(0,0,0,0.2);
    background-blend-mode: multiply;
    background-image: url('/images/package.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom center;
    width: 100%;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

#package-header h3{
    text-transform: uppercase;
    color: white;
    font-size: 52px;
    font-weight: bold;
}

@media(max-width: 700px){
    #package-header {
        background-color: rgba(0,0,0,0.3);
        height: 25vh;
    }

    #package-header h3 {
        font-size: 22px;
    }

}

#package-body {
    margin-top: 24px;
}

/* box-shadow: 
2px 5px 10px -8px rgba(0,0,0,0.35),
-5px -5px 10px -10px rgba(0,0,0,0.3); */

.package-content {
    height: auto;
    /* border: 1px solid black; */
}

#package-duration, #similar-packages, #package-overview, #package-features, #package-inclusions, #package-itenary{
    width: 100%;
    height: auto;
    padding: 5px 20px;
}

.box-container {
    width: 100%;
}

.boxes {
    display: inline-flex;
    align-items: center;
    min-height: 40px;
}

.line {
    width: 0.4px;
    background: #707070;
    height: 35px;
}

.box {
    display: flex;
    align-items: center;
    margin-left: 14px;
}

.secondary-text {
    color: #6F6F6F;
    font-size: 24px;
}

.inner-text {
    color: black;
    font-weight: 600;
    padding-right: 14px;
    font-size: 18px;
}

.box h6 {
    margin: 0px !important;
}

.box:last-child > .line{
    display: none;
}

.package-datas {
    display: flex;   
}


.special-box {
    margin-top: 24px;
    margin-bottom: 12px;
}

@media(min-width: 500px){

    .special-box .boxes {
        position: relative;
    }

    .special-box .boxes::before{
        content: '';
        position: absolute;
        transform: translateX(-50%);
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: red;
    }
    
    .special-box .boxes::after{
        content: '';
        position: absolute;
        right: 0px;
        transform: translateX(50%);
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: #10FF00;
    }
}


@media(max-width: 700px){

    .package-datas {
        flex-direction: column;
    }

    .special-box {
        margin-top: 0px;
        margin-bottom: 0px;
    }

    .special-box > .boxes{
        display: flex;
        flex-wrap: wrap;
    }

    .special-box .z-depth-1{
        box-shadow: none !important;
    }

    .special-box .box {
        margin: 0px;
        margin-right: 8px;
        margin-top: 12px;
        min-height: 40px;
        box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    }

    .special-box .line{
        display: none;
    }

    .special-box .inner-text {
        padding-right: 0px;
        padding: 0px 10px;
    }

    .special-box .box {
        position: relative;
    }

    .special-box .box:nth-child(1)::before {
        content: '';
        position: absolute;
        transform: translateX(-50%);
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: red;
    }

    .special-box .box:last-child::after {
        content: '';
        position: absolute;
        right: 0px;
        transform: translateX(50%);
        border-radius: 50%;
        width: 15px;
        height: 15px;
        background: #10FF00;
    }

    .box-container {
        margin-bottom: 12px;
    }

    .secondary-text {
        font-size: 18px;
    }

    .inner-text {
        font-size: 16px;
    }

}



.similar-package {
    height: auto;
    /* border: 1px solid black; */
}

.sim-pac {
    margin-top: 24px;
}

.sim-pac h6 , .sim-pac p{
    margin: 0px;
    padding: 0px;
}

.sim-pac img{
    display: block;
    width: 100%;
    height: 25vh;
    object-fit: cover;
}

@media(max-width: 700px){
    .similar-package {
        margin-top: 24px;
    }
}

#package-overview , #package-features, #package-inclusions, #package-itenary{
    margin-top: 24px;
}

.pac-display-text {
    color: var(--primaryColor);
    font-weight: 600;
    font-size: 22px;
}

.inclusions {
    margin-top: 10px;
    display: flex;
    width: 100%;
    /* justify-content: space-between; */
}

.inclusion {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 12px;
}

.inclusion span {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: 0.4px solid rgb(172, 172, 172);
    color: var(--primaryColor);
}

.inclusion p{
    font-size: 12px;
    margin-top: 5px !important;
}

.inclusion span:hover {
    color: white;
    background: var(--secondaryColor);
}

.pac-inclusion {
    display: flex;
    align-items: center;
    margin-top: 12px;
}

.pac-inclusion h6 {
    margin: 0px;
    padding: 0px;
    margin-left: 12px;
    color: #6F6F6F;
    font-size: 16px;
    font-weight: 600;
}

.circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: var(--secondaryColor);
}

.itenary-item {
    display: flex;
    align-items: flex-start;
    margin-top: 24px;
}

.itenary-item p, .itenary-item h6{
    margin: 0px;
    padding: 0px;
}

.itenary-item h6 {
    font-weight: 600;
    color: #6F6F6F;
}

.itenary-item p {
    margin-top: 5px;
    color: #707070;
}

.itenary-item img {
    width: 45px;
    height: 45px;
    margin-right: 12px;
}

.tour-button {
    position: fixed;
    bottom: 5vh;
    right: 5%;
    background: var(--primaryColor);
    padding: 20px 40px;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 24px;
    animation: tourButtonAnimation 1.4s ease-in-out infinite alternate-reverse;
}

@keyframes tourButtonAnimation {
    0%{
        background: var(--primaryColor);
    }
    100%{
        background: var(--secondaryColor);
    }
}

@media(max-width: 700px){
    .tour-button {
        width: 100%;
        right: 0px;
        bottom : 0px;
        border-radius: 0px;
        font-size: 18px;
        letter-spacing: 2px;
        animation: none;
    }
}